<template>
  <div class="markdown-body" style="width: 100%; text-align: start">
    <article v-html="html"></article>
  </div>
</template>
<script>
import markdown from "marked";
import axios from "axios";
export default {
  data() {
    return {
      html: "",
    };
  },
  methods: {
    parase_markdown(markdown_context) {
      this.html = markdown(markdown_context);
    },
    getData() {
      const name = this.$route.params.post_name;
      const url = this.$store.state.base_url + `/posts/${name}`;
      axios.get(url).then((response) => {
        this.parase_markdown(response.data);
      });
    },
  },
  mounted() {
    this.getData();
  },
};
</script>
